.wrapper {
  width: 75%;
  max-width: 1200px;
  margin: 0 auto;
}
#nav {
  display: flex;
  padding: 20px;
  justify-content: space-between;
}

#site-title {
 font-family: 'Karla', sans-serif;
 font-size: 1.19em;
 font-weight: 700;
 color: black;
 align-items: center;
}

#nav-items {
  display: flex;
  align-items: center;
}

.nav-item {
  padding: 0 15px;
  font-family: 'Lato', sans-serif;
}

ul li a {
  text-decoration: none;
  color: #000;
}

#nav-items a:hover {
  color: #8ecae6;
}

@media only screen and (max-width: 695px) {
  #nav {
    flex-direction: column;
    align-items: center;
  }

  #site-title {
    padding-bottom: 12px;
  }
}


#img-container {
  overflow: hidden;
}

#header-img {
  max-width: 100%;
}
#header-section {
  display: flex;
  flex-flow: row nowrap;
  padding-top: 30px;
  justify-content: center;
  width: 100%;
  align-content: stretch;
}

#header-content {
  align-self: center;
  margin-left: 15px;
  font-family: "Lato", sans-serif;
}

#header-content .highlight {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 20%, #8ecae6 130%);
  font-weight: 950;
}

.header-text {
  line-height: 1.2;
  margin: 12px 0;
  display: block;
}

@media only screen and (max-width: 806px) {
  #header-section {
    padding-top: 0px;
    display: flex;
    flex-flow: column wrap;
  }

  .header-title {
    margin-top: 10px;
  }
}

@media only screen and (max-width: 800px) and (min-width: 716px) {
  .header-text {
    font-size: 0.9em;
  }
}

.project-card {
  background: hsl(199, 64%, 73%);
  height: auto;
  width: 454px;
  margin: 20px 15px;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
}

.project-img-container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px;
}

.project-img {
  width: auto;
  height: auto;
  margin: 10px;
  width: 100%;
}

.project-card-content {
  padding-right: 15px;
  padding-left: 15px;
  padding-bottom: 15px;
}

.project-links {
  font-size: 25px;
}

a.url-link {
  color: #000;
}

a.github-link {
  color: #000;
}

.project-links a i:hover {
  color: #fff;
}

.github-link {
  margin-left: 3px;
}

.project-card-content {
  padding-bottom: 0px;
}

.project-title {
  font-size: 2rem;
  margin-top: 5px;
}

.project-title-highlight {
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0) 40%,
    rgb(255, 255, 255) 47%
  );
}

.tech-featured {
  font-weight: bold;
}

.project-topics {
  line-height: 1.2;
  display: block;
  margin: 15px 0;
  font-family: "Lato", sans-serif;
}

.project-description {
  line-height: 1.2;
  display: block;
  margin: 15px 0;
  font-family: "Lato", sans-serif;
}

@media only screen and (min-width: 806px) and (max-width: 915px) {
  .project-card {
    width: 272px;
  }
}

@media only screen and (min-width: 916px) and (max-width: 930px) {
  .project-card {
    width: 310px;
  }
}

@media only screen and (min-width: 931px) and (max-width: 1015px) {
  .project-card {
    width: 319px;
  }
}

@media only screen and (min-width: 1016px) and (max-width: 1146px) {
  .project-card {
    width: 351px;
  }
}

@media only screen and (min-width: 1147px) and (max-width: 1302px) {
  .project-card {
    width: 400px;
  }
}
.project-cards-container {
  padding: 0;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
.title {
  margin: 25px auto;
  display: inline-block;
  padding: 10px;
  border-bottom: 1px solid black;
  font-family: 'Lato', sans-serif;;
}

.section-title {
  text-align: center;
}



#about-img-container {
  overflow: hidden;
  border-radius: 10px;
}

#about-img {
  width: 100%;
}

@media only screen and (max-width: 806px){
  #about-img-container {
    width: 300px;
  }
}

@media only screen and (min-width:807px) and (max-width: 1038px){
  #about-img-container {
    width: 200px;
  }
}

@media only screen and (min-width:1039px){
  #about-img-container {
    width: 300px;
  }
}

