#about-section {
  display: flex;
  flex-flow: row-reverse nowrap;
  justify-content: center;
  width: 100%;
  overflow: hidden;
  align-items: center;
}

#about-content {
  align-self: center;
  font-family: "Lato", sans-serif;
  margin-bottom: 10px;
  margin-left: 10px;
  margin-right: 10px;
}

.about-me-text {
  line-height: 1.2;
  margin: 12px 0;
  display: block;
}

@media only screen and (max-width: 806px) {
  #about-section {
    display: flex;
    flex-flow: column-reverse wrap;
  }
}

#contact-content {
  text-align: center;
}

.contact-text {
  line-height: 1.2;
  display: block;
  margin-bottom: 17px;
  font-family: "Lato", sans-serif;
}

.email-tag {
  margin-bottom: 12px;
  font-family: "Lato", sans-serif;
}

.email {
  text-decoration: none;
  font-weight: bold;
  color: #000;
}

a.email:hover {
  color: #8ecae6;
}

.contact-links {
  font-size: 35px;
}

.contact-icon {
  margin: 10px;
  color: #000;
}

.resume-icon i:hover {
  color: #8ecae6;
}

.github-icon i:hover {
  color: #8ecae6;
}

.linkedin-icon i:hover {
  color: #8ecae6;
}
.footer-content {
  margin: 30px 0;
  display: flex;
  justify-content: space-between;
}

.footer-text {
  text-align: center;
  font-family: "Lato", sans-serif;
  font-size: .3em;
}

@media only screen and (max-width: 1076px) {
  .footer-content {
    flex-flow: column wrap;
    align-items: center;
  }
}
.scroll-button {
  position: fixed; 
   width: 100%;
   left: 90%;
   bottom: 40px;
   height: 20px;
   font-size: 3rem;
   z-index: 1;
   cursor: pointer;
   color: #8ecae6;
}

@media only screen and (max-width: 600px) {
  .scroll-button {
    display: none;
  }
}

